@import 'ScssHelpers/_helpers_all.scss';

.not_found {
	@extend %global_flex_block_column_nowrap_center_center;

	width: 100%;

	background: var(--primary_dark_color);

	overflow-x: hidden;

	@include bp($point_4, min) {
		padding: calc(var(--height-header) + var(--gl-indent)) var(--gl-indent) var(--gl-indent);
		height: 100vh;
	}

	@include bp($point_4 - 1) {
		padding: var(--gl-indent);
		height: calc(var(--full-screen) - var(--height-header) - var(--height-cookie-mob));

		.body--mobile_cookie & {
			height: calc(var(--full-screen) - var(--height-header));
		}
	}
}

.not_found_title {
	line-height: 1;
	font-family: $biotif_font;
	letter-spacing: .04em;

	@include bp($point_4, min) {
		font-size: 19rem;
	}

	@include bp($point_4 - 1) {
		font-size: 10rem;
	}

	&:not(:last-child) {
		margin-bottom: -2rem;

		.is_safari & {
			margin-bottom: 0.5rem;
		}
	}
	
}

.not_found_title__char {
	color: var(--white_color);

	&.not_found_title__char--color_1 {
		color: var(--orange_color);
	}

	&.not_found_title__char--color_2 {
		color: var(--green_color);
	}

	&.not_found_title__char--color_3 {
		color: var(--primary_light_color);
	}
}

.not_found_subtitle {
	line-height: 1.5;
	font-family: $biotif_font;
	color: var(--white_color);

	@include bp($point_4, min) {
		font-size: 2.8rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
	}

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 14.2rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 4rem;
		}
	}
}

.not_found_link {
	@extend %transition_color;

	font: 2rem / 2.1 $biotif_font;
	color: var(--blue_color_2);

	&:hover {
		text-decoration: none;
		color: var(--orange_color);
	}

	&:focus {
		text-decoration: none;
	}
}